<template>
  <div class="msg-item" @click="clickMessage">
    <p class="title"><span :class="{read: !message.read_time}">{{message.read_time ? '(已读)' : '(未读)'}}</span>{{message.message_title}}</p>
    <p>{{message.create_time}}</p>
  </div>
</template>

<script>
export default {
  name: 'Dealer-Message-Item',
  props: {
    message: Object
  },
  methods: {
    clickMessage () {
      this.$parent.$emit('clickOneMessageFromDealer', this.message)
    }
  }
}
</script>

<style lang="less" scoped>
.msg-item {
  padding: .5rem 1rem;
  border-bottom: 1px solid #eee;
  &:first-child {
    padding-top: 0;
  }
  p.title {
    margin: 0;
    color: #333;
    font-weight: 500;
    font-size: 14px;
  }
  p {
    margin: 0;
    font-size: 13px;
    color: #999;
  }
}
.read {
  color: red;
}
</style>



