<template>
  <div class="wrap">
    <app-header title="消息中心" showBack="true" />
    <div class="main">
      <message-item
          v-for="(item, index) in messages"
          :key="item.message_id"
          :message="item"
      ></message-item>
    </div>
    <mt-popup
      style="border-radius: 10px; width: 80%;"
      v-model="popupVisible"
      pop-transition="popup-fade">
      <message-detail :message="selected" />
    </mt-popup>
  </div>
</template>

<script>
import AppHeader from '../../components/header/header'
import MessageItem from './dealer-message-item'
import MessageDetail from './dealer-message-detail'
import bus from '../../assets/js/bus'
import {getDealerMessageList, readMessage, removeDealerMessage} from '../../api/maintain'
export default {
  name: 'Dealer-Message',
  data () {
    return {
      messages: [],
      popupVisible: false,
      selected: null
    }
  },
  created () {
    this.getMessageList()
  },
  mounted () {
    this.$on('clickOneMessageFromDealer', (message) => {
      this.popupVisible = true
      this.selected = message
      readMessage(message.message_id)
        .then(data => {
          if (data.state) {
            this.getMessageList()
          }
        })
    })

    bus.$on('clickRemoveOneMessageFromDealer', (message) => {
      removeDealerMessage(message.message_id)
        .then(data => {
          if (data.state) {
            this.getMessageList()
            this.$toast(data.errormsg || '删除成功')
            this.popupVisible = false
          } else {
            this.$toast(data.errormsg || '删除失败')
          }
        })
        .catch(err => {
          console.log(err)
          this.$toast('网络错误，请重试!')
        })
    })
    bus.$on('clickOk', () => {
      this.popupVisible = false
    })
  },
  methods: {
    getMessageList () {
      getDealerMessageList()
        .then(data => {
          if (data.state) {
            this.messages = data.data.list
          } else {
            this.$toast(data.errormsg || '获取数据失败')
          }
        })
        .catch(err => {
          console.log(err)
          this.$toast('网络错误，请重试!')
        })
    }
  },
  components: {AppHeader, MessageItem, MessageDetail}
}
</script>

<style lang="less" scoped>
.wrap {
  height: 100%;
  padding: 55px 0;
  background: #fff;
  .main {}
}
</style>



