<template>
  <div class="detail">
    <div class="cont">{{message.content}}</div>
    <div style="text-align: center;">
      <mt-button type="danger" @click="clickRemove">删除</mt-button>
      <mt-button type="primary" @click="clickOk">确定</mt-button>
    </div>
  </div>
</template>

<script>
import bus from '../../assets/js/bus'
export default {
  name: 'VehicleDetail',
  props: {
    message: Object
  },
  destroyed () {
    bus.$off('clickRemoveOneMessageFromDealer')
    bus.$off('clickOk')
  },
  methods: {
    clickRemove () {
      bus.$emit('clickRemoveOneMessageFromDealer', this.message)
    },
    clickOk () {
      bus.$emit('clickOk')
    }
  }
}
</script>

<style lang="less" scoped>
  .detail{
    min-height: 150px;
    position: relative;
    .cont{
      padding: 1rem;
    }
    a {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      padding: 1rem;
      text-align: center;
      color: red;
    }
  }
</style>



